@import url('https://fonts.googleapis.com/css2?family=Plus+Jakarta+Sans:wght@500;800&display=swap');

*, *::before, *::after{
    box-sizing: border-box;
}
*{
    margin: 0;
}
body{
    font-family: "Plus Jakarta Sans";
    text-align: center;
}
em{
    font-style: normal;
}
button{
    outline: 0;
    border: none;
    background: none;
    color: black;
    font-size: 1rem;
    font-family: inherit;
}
.container{
    margin: 1rem;
}
.header{ 
    margin: 1rem;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
}
.header h1{
    font-size: 1.25rem;
    margin-right: 0.5rem;
    font-weight: 800;
}
.header p{
    font-size: 0.85rem;
    font-weight: 200;
    color: hsl(219, 12%, 42%);
}
.counter{
    display: flex;
    flex-direction: column;
    justify-content: center;
    background:hsl(219, 85%, 26%);
    color: white;
    width: 30px;
    height: 25px;
    border-radius: 7px;
}
.clear:active{
    color: rgb(4, 4, 62);
    font-weight: bold;
    transition: 10000s;
}