.notifications{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.notification{
    display: flex;
    flex-direction: row;
    background: hsl(210, 60%, 98%);
    padding: 0.85rem;
    border-radius: 8px;
    margin-bottom: 1rem;
}
.icon{
    margin-right: 0.85rem;
}
.icon img{
    width: 60px;
}
.details{
    color: hsl(219, 14%, 63%);
    text-align: left;
    /* word-wrap: break-word; */
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 100%;
}
.title{
    font-weight: bold;
    color: black;
    padding-right: 0.25rem;
}
.comment{
    padding-right: 0.25rem;
}
.body{
    color: hsl(224, 21%, 14%);
    font-size: 0.95rem;
    color: hsl(219, 12%, 42%);
    font-weight: 600;
}   
.time{
    font-size: 0.85rem;
}
.title:hover, .body:hover{
    color: rgb(4, 4, 62);
    font-weight: bold;
}